<h2 id="新建md文件后-首页不显示" tabindex="-1"><a class="header-anchor" href="#新建md文件后-首页不显示" aria-hidden="true">#</a> 新建md文件后，首页不显示<Badge type="tip" text="针对于v1.3.0以后版本" vertical="top" /></h2>
<p>有两个原因</p>
<ol>
<li>
<p>首先看你新建的这个md文件的位置是否正确，<code>.md</code>文件，需要放在<code>docs</code>目录下，注意是放在<code>docs</code>目录下，不能放在<code>docs/.vuepress</code>目录下</p>
</li>
<li>
<p>该<code>.md</code>文件确实放在<code>docs</code>目录下，但是你没有重新运行<code>npm run dev</code>命令，所以也不会在首页显示</p>
</li>
</ol>
<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p>在<code>docs</code>目录下，新建md文件，或者是修改<code>docs/.vuepress/config.js</code>内的配置后，都需要重新运行<code>npm run dev</code>才可以生效</p>
</div>
<h2 id="如果编写文章" tabindex="-1"><a class="header-anchor" href="#如果编写文章" aria-hidden="true">#</a> 如果编写文章？</h2>
<p>因为此博客的所有文章都是存放在<code>docs</code>目录下的，vuepress会将所有在此<code>docs</code>目录下的md文件，自动解析成<code>.html</code>文件</p>
<blockquote>
<p>注意，这里说的是<code>docs</code>目录，你不要放在<code>docs/.vuepress</code>目录下，而且你需要重新运行<code>npm run dev</code>命令才能够生效</p>
</blockquote>
<p>你需要学习一下<a href="https://www.runoob.com/markdown/md-title.html" target="_blank" rel="noopener noreferrer">markdown语法<ExternalLinkIcon/></a>，除此以外，推荐你下载<a href="https://typora.io/" target="_blank" rel="noopener noreferrer">Typora<ExternalLinkIcon/></a>，此软件书写md文件，非常棒，你如果不想看文章，也可以去bilibili搜索如何使用typora软件教程</p>
<h2 id="如何部署" tabindex="-1"><a class="header-anchor" href="#如何部署" aria-hidden="true">#</a> 如何部署？？？</h2>
<p>关于如何部署这个问题，你需要先了解vuepress是一个静态网站生成框架，当运行<code>npm run build</code>命令之后，vuepress会将<code>docs</code>目录下的所有<code>.md</code>文件(<code>不包括docs/.vuepress目录内的md文件</code>)自动解析成<code>.html</code>文件，并且<code>自动</code>放在<code>docs/.vuepress/dist</code>目录下，此目录下，所有的html和js等，便是你博客的所有页面和js等文件</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211115232524302" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211115232524302.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>所以了解了vuepress的基本过程，那么部署我们自己的博客，其实也就是如何部署<code>docs/.vuepress/dist</code>目录下的所有静态html文件，所以你就不要再让我一步一步的教你如何部署这些静态文件！！，有以下几个途径，但是不局限于这些，仅仅是我目前知道的，你也可以直接看<a href="https://v2.vuepress.vuejs.org/zh/guide/deployment.html#netlify" target="_blank" rel="noopener noreferrer">vuepress官方<ExternalLinkIcon/></a>的</p>
<ol>
<li>
<p>使用云服务器部署</p>
<blockquote>
<p>如你可以直接将<code>docs/.vuepress/dist</code>目录下的所有文件，直接上传到你云服器某个域名空间下，直接使用域名就可以访问</p>
</blockquote>
</li>
<li>
<p>使用<code>Github </code>进行托管</p>
<blockquote>
<p><a href="https://aurora.xcye.xyz/home/deploy.html" target="_blank" rel="noopener noreferrer">部署<ExternalLinkIcon/></a>，其实也就是将<code>docs/.vuepress/dist</code>目录下的所有文件，push到github的某个仓库，某个分支中，然后你就可以简单设置一下，就可以通过github给的一个免费域名(<code>https://&lt;user&gt;.github.io/&lt;repo&gt;</code>)直接访问你博客了</p>
</blockquote>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211115233623578" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211115233623578.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
</li>
<li>
<p>使用<a href="https://vercel.com/guides/deploying-vuepress-to-vercel" target="_blank" rel="noopener noreferrer">vercel<ExternalLinkIcon/></a>，这个并不是部署静态资源，如果你需要使用vercel进行部署的话，你需要将你博客的索赔由于文件(<code>除node_modules</code>)都push到github，然后在vercel中导入你push到github的这个仓库，然后设置一下部署流程就行</p>
<blockquote>
<p>记住，是所有除<code>node_modules</code>外的所有文件及文件夹，包括<code>docs</code>下的所有，<code>package.json</code>文件等等</p>
</blockquote>
</li>
<li>
<p>还有其他很多的静态资源部署方式</p>
</li>
</ol>
<h2 id="github-actions部署是什么" tabindex="-1"><a class="header-anchor" href="#github-actions部署是什么" aria-hidden="true">#</a> Github Actions部署是什么？？</h2>
<p>如果你需要使用Github Actions进行部署，那么你的先知道<code>Github Actions</code>是什么！！</p>
<blockquote>
<p>我们可以在Github中持续集成很多操作方法，比如抓取代码、运行测试、登录远程服务器，发布到第三方服务等等，把这些操作就称为 actions。</p>
<p>所以<code>Github Actions</code>也就是会自动执行你设置的一些命令，按照你<code>.github\workflows</code>目录内编写的文件自动执行</p>
<p>如果你还想要链接更多的Github Actions信息，推荐你看一下<a href="http://www.ruanyifeng.com/blog/2019/09/getting-started-with-github-actions.html" target="_blank" rel="noopener noreferrer">GitHub Actions 入门教程 - 阮一峰的网络日志 (ruanyifeng.com)<ExternalLinkIcon/></a></p>
</blockquote>
<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p>所以如果需要使用<code>Github Actions</code>完成自动部署，那么你就必须将你博客项目中，除了<code>node_modules</code>文件夹之外的所有文件及文件夹，全部都push到github的某个分支中(<code>看你喜好，我的是main分支</code>)，然后github只要检测到你该分支中的内容发生改变，就会自动触发<code>.github/workflows/</code>目录下某个yaml(<code>或者yml</code>)文件，自动执行一下命令</p>
<ol>
<li><code>npm run build</code> 目的: 在dist目录中，生成你博客的所有html及其js，css灯光</li>
<li><code>进入dist</code></li>
<li><code>初始化该dist目录，然后自动将该目录内的所有html及其他文件push到该仓库中的某个分支中</code></li>
</ol>
<p>一般最开始使用<code>Github Actions</code>都会出错，你可以看到他执行的过程，如果出错的话，请多试几次(<code>我最开始也是这样</code>)，然后在对照其执行过程，看看哪一步出错，自己先尝试解决吧</p>
</div>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211116084812735" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211116084812735.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211116084840153" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211116084840153.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211116085044115" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211116085044115.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211116085230372" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211116085230372.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211116085338792" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211116085338792.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p>如果是技术小白的话，使用github Actions出错，也就是状态是红色，请直接在<a href="https://jq.qq.com/?_wv=1027&amp;k=R7DF9XtV" target="_blank" rel="noopener noreferrer">QQ群<ExternalLinkIcon/></a>里进行询问，或者联系我<a href="tencent://message/?uin=2291308094">QQ</a>，如果是非技术小白，请先看一下哪一步出错，看一下自己push到仓库的文件是否完整，如果你克隆我github，那么一般会报个<code>theme not found</code>，请看一下<code>docs/.vuepress/config.js</code>中的<code>theme</code>项使用的是本地主题还是npm安装的主题，插件也会存在这个问题，如果多次都解决不了请直接在<a href="https://jq.qq.com/?_wv=1027&amp;k=R7DF9XtV" target="_blank" rel="noopener noreferrer">QQ群<ExternalLinkIcon/></a>里进行询问，或者联系我<a href="tencent://message/?uin=2291308094">QQ</a></p>
</div>
<h2 id="关于静态图片" tabindex="-1"><a class="header-anchor" href="#关于静态图片" aria-hidden="true">#</a> 关于静态图片</h2>
<p>如果在博客中，访问本地的图片，在介绍之前，请一定查看<a href="https://v2.vuepress.vuejs.org/zh/guide/assets.html" target="_blank" rel="noopener noreferrer">官方<ExternalLinkIcon/></a>的说明，自己先写个md文件，引用本地图片</p>
<h2 id="安装依赖-主题之后-没有config-js等文件" tabindex="-1"><a class="header-anchor" href="#安装依赖-主题之后-没有config-js等文件" aria-hidden="true">#</a> 安装依赖，主题之后，没有config.js等文件</h2>
<p>除了<code>package.json,package-lock.json,node_modules</code>文件及目录之外，其余的所有文件夹以及文件都是需要你自己创建</p>
<p>运行必须的文件及文件夹有</p>
<table>
<thead>
<tr>
<th>路径</th>
<th>类型</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>docs</td>
<td>文件夹</td>
<td></td>
</tr>
<tr>
<td>docs/.vuepress</td>
<td>文件夹</td>
<td>放置配置文件，临时文件，build后的文件</td>
</tr>
<tr>
<td>docs/.vuepress/config.js</td>
<td>文件</td>
<td>主题及站点配置文件</td>
</tr>
<tr>
<td>docs/.vuepress/public</td>
<td>文件夹</td>
<td>静态文件，可以放置图片等，访问<code>localhost[端口]/静态资源文件名</code></td>
</tr>
<tr>
<td>docs/.vuepress/styles</td>
<td>文件夹</td>
<td>覆盖及自定义css样式</td>
</tr>
<tr>
<td>docs/.vuepress/styles/index.css</td>
<td>文件</td>
<td>可以在此css文件内，覆盖主题的所有样式</td>
</tr>
<tr>
<td>docs/.vuepress/styles/palette.css</td>
<td>文件</td>
<td>定义css变量，可以在index.css文件内，使用这些定义的变量</td>
</tr>
</tbody>
</table>
<h2 id="需要vue知识" tabindex="-1"><a class="header-anchor" href="#需要vue知识" aria-hidden="true">#</a> 需要vue知识？</h2>
<p>如果你只是搭建一个博客，不想对博客进行一些功能上的升级，那么不需要vue知识，只需要安装<code>node</code>，运行几个命令就可以了，所有的配置都可以在<code>docs/.vuepress/config.js</code>文件内进行配置</p>
<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p>注意需要的命令(<code>前提你直接复制https://aurora.xcye.xyz/readme/下的scripts内容</code>)</p>
<ul>
<li>
<p><code>npm run dev</code></p>
<blockquote>
<p>在本地运行，比如你写md文件的时候，可以运行起来，看看效果</p>
</blockquote>
</li>
<li>
<p><code>npm run build</code></p>
<blockquote>
<p>在<code>docs/.vuepress/dist</code>目录下，生成包含<code>index.html</code>等页面内容</p>
</blockquote>
</li>
</ul>
</div>
<h2 id="主题如何升级" tabindex="-1"><a class="header-anchor" href="#主题如何升级" aria-hidden="true">#</a> 主题如何升级</h2>
<p>前提是你没有使用本地主题</p>
<details class="custom-container details"><summary>本地主题?</summary>
<p>如果<code>config.js</code>下的theme像下面这样，那么你使用的就是本地主题</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token literal-property property">theme</span><span class="token operator">:</span> path<span class="token punctuation">.</span><span class="token function">resolve</span><span class="token punctuation">(</span>__dirname<span class="token punctuation">,</span> <span class="token string">"theme/lib/node/index.js"</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>如果使用下面这个样子，那么就是使用的是npm安装的主题</p>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>theme: 'aurora',
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div></details>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211026223646160" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211026223646160.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<blockquote>
<p>主题最新版，可以在GitHub中查看<a href="https://github.com/vuepress-aurora/vuepress-theme-aurora/releases" target="_blank" rel="noopener noreferrer">releases<ExternalLinkIcon/></a>或者在<a href="https://www.npmjs.com/package/vuepress-theme-aurora" target="_blank">npm</a>查看</p>
</blockquote>
<h2 id="在首页没有显示最后更新时间" tabindex="-1"><a class="header-anchor" href="#在首页没有显示最后更新时间" aria-hidden="true">#</a> 在首页没有显示最后更新时间</h2>
<p>因为文章的最后更新时间，是根据你<code>git commit</code>获取的，所以你需要在<code>package.json</code>同级目录下，初始化git仓库，或者在<code>docs</code>目录下，最后<code>git commit</code>之后就可以了</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211111092140467" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211111092140467.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>然后输入命令</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">git</span> init <span class="token operator">&amp;&amp;</span> <span class="token function">git</span> <span class="token function">add</span> <span class="token builtin class-name">.</span> <span class="token operator">&amp;&amp;</span> <span class="token function">git</span> commit -m <span class="token string">"update"</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="如何部署到服务器上" tabindex="-1"><a class="header-anchor" href="#如何部署到服务器上" aria-hidden="true">#</a> 如何部署到服务器上？</h2>
<ul>
<li>你可以运行<code>npm run build</code>命令之后，会在<code>docs/.vuepress/dist</code>目录下，生成所有文章的HTML文件，你可以直接将此文件内的内容，像静态页面那样，部署到服务器中</li>
<li>如果你使用GitHub进行托管，那么你可以看一下这篇文章https://v2.vuepress.vuejs.org/zh/guide/deployment.html，我推荐使用GitHub Actions进行自动部署<a href="https://aurora.xcye.xyz/home/deploy.html" target="_blank" rel="noopener noreferrer">详细<ExternalLinkIcon/></a></li>
<li>你也可以使用其他方式进行部署</li>
</ul>
<h2 id="如何自定义主题样式" tabindex="-1"><a class="header-anchor" href="#如何自定义主题样式" aria-hidden="true">#</a> 如何自定义主题样式</h2>
<p>你可以根据自己的喜好，对主题的css样式进行修改，在<code>docs/.vuepress/styles/index.css</code>文件内的样式，会覆盖注意的默认样式，所以你可以直接在<code>docs/.vuepress/styles/index.css</code>内修改需要改变的样式</p>
<h2 id="什么是front-matter" tabindex="-1"><a class="header-anchor" href="#什么是front-matter" aria-hidden="true">#</a> 什么是Front Matter</h2>
<p>在md文件中，你可以设置是否置顶，设置更新时间，设置标签等等，都需要使用到<code>frontMatter</code>，这个需要使用<code>yaml</code>语法，详细可以查看官方<a href="https://v1.vuepress.vuejs.org/zh/guide/frontmatter.html">Front Matter</a></p>
<details class="custom-container details"><summary>实例</summary>
<div class="language-markdown ext-md line-numbers-mode"><pre v-pre class="language-markdown"><code><span class="token front-matter-block"><span class="token punctuation">---</span>
<span class="token front-matter yaml language-yaml"><span class="token key atrule">stick</span><span class="token punctuation">:</span> <span class="token boolean important">false</span> <span class="token comment">#是否置顶</span>
<span class="token key atrule">categories</span><span class="token punctuation">:</span> <span class="token punctuation">[</span>首页配置<span class="token punctuation">,</span>一言<span class="token punctuation">,</span>头像<span class="token punctuation">,</span>社交<span class="token punctuation">,</span>页脚<span class="token punctuation">]</span> <span class="token comment">#标签</span>
<span class="token key atrule">date</span><span class="token punctuation">:</span> <span class="token string">"2021/10/19 21:18"</span> <span class="token comment">#手动设置最后更新时间，如果有git commit，将使用该值作为最后更新时间</span>
<span class="token key atrule">layout</span><span class="token punctuation">:</span> RegisterCom <span class="token comment">#为该md设置layout</span></span>
<span class="token punctuation">---</span></span>

<span class="token title important"><span class="token punctuation">#</span> 这是一个FrontMatter演示</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div></details>
<h2 id="报错解决" tabindex="-1"><a class="header-anchor" href="#报错解决" aria-hidden="true">#</a> 报错解决</h2>
<h3 id="更换主题之后-浏览器报错uncaught-in-promise-referenceerror-cannot-access-clientappenhances-before-initialization" tabindex="-1"><a class="header-anchor" href="#更换主题之后-浏览器报错uncaught-in-promise-referenceerror-cannot-access-clientappenhances-before-initialization" aria-hidden="true">#</a> 更换主题之后，浏览器报错<code>Uncaught (in promise) ReferenceError: Cannot access 'clientAppEnhances' before initialization</code></h3>
<p>如果你更换主题之后，打开浏览器，报下面错误</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211012082239703" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211012082239703.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211012082253643" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211012082253643.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>这个错误我暂时不知道是因为什么造成的，而且，当遇到这个错误，你在<code>config.js</code>将下面内容全部注释之后，使用默认主题，也会出现这个错误</p>
<details class="custom-container details"><summary>注释内容</summary>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">//const { themeConfig } = require("./themeConfig");</span>
module<span class="token punctuation">.</span>exports <span class="token operator">=</span> <span class="token punctuation">{</span>
    <span class="token literal-property property">plugins</span><span class="token operator">:</span> <span class="token punctuation">[</span>
        <span class="token punctuation">[</span>
            <span class="token string">'@vuepress/plugin-search'</span><span class="token punctuation">,</span>
            <span class="token punctuation">{</span>
                <span class="token literal-property property">locales</span><span class="token operator">:</span> <span class="token punctuation">{</span>
                    <span class="token string-property property">'/'</span><span class="token operator">:</span> <span class="token punctuation">{</span>
                        <span class="token literal-property property">placeholder</span><span class="token operator">:</span> <span class="token string">'Search'</span><span class="token punctuation">,</span>
                    <span class="token punctuation">}</span><span class="token punctuation">,</span>
                    <span class="token string-property property">'/zh/'</span><span class="token operator">:</span> <span class="token punctuation">{</span>
                        <span class="token literal-property property">placeholder</span><span class="token operator">:</span> <span class="token string">'搜索'</span><span class="token punctuation">,</span>
                    <span class="token punctuation">}</span><span class="token punctuation">,</span>
                <span class="token punctuation">}</span><span class="token punctuation">,</span>
            <span class="token punctuation">}</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token literal-property property">head</span><span class="token operator">:</span> <span class="token punctuation">[</span>
        <span class="token punctuation">[</span>
            <span class="token string">"script"</span><span class="token punctuation">,</span>
            <span class="token punctuation">{</span>
                <span class="token literal-property property">src</span><span class="token operator">:</span> <span class="token string">"https://at.alicdn.com/t/font_2849934_v6y652peian.js"</span><span class="token punctuation">,</span>
            <span class="token punctuation">}</span><span class="token punctuation">,</span>
        <span class="token punctuation">]</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token comment">//theme: 'aurora',</span>
    <span class="token literal-property property">title</span><span class="token operator">:</span> <span class="token string">'一款简洁.美观.功能强大的vuepress主题'</span><span class="token punctuation">,</span>
    <span class="token comment">//themeConfig: {}</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br></div></div></details>
<h4 id="解决" tabindex="-1"><a class="header-anchor" href="#解决" aria-hidden="true">#</a> 解决</h4>
<p>我目前的解决方案为：</p>
<ul>
<li>1.删除<code>docs/.vuepress/.cache</code>和<code>docs/.vuepress/.temp</code>两个文件夹</li>
</ul>
<img src="https://ooszy.cco.vin/img/blog-note/image-20211012082533295.png?x-oss-process=style/pictureProcess1" alt="image-20211012082533295" style="zoom: 50%;" />
<ul>
<li>2.删除<code>node_modules</code>文件夹</li>
<li>3.注释<code>docs/.vuepress/config.js</code>内的全部内容，或者你可以删除此文件夹内的全部内容</li>
<li>4.如果你打开编辑器，那么关闭编辑器</li>
<li>5.重新启动编辑器，或者是命令面板</li>
<li>6.重新运行<code>npm install</code>，待此命令执行成功之后</li>
<li>7.将<code>docs/.vuepress/config.js</code>取消注释，或者添加你刚才删除的数据</li>
<li>8.直接执行命令<code>npm run dev</code></li>
</ul>
<p>便可以解决</p>
<h3 id="运行npm-run-dev之后-报下面错误" tabindex="-1"><a class="header-anchor" href="#运行npm-run-dev之后-报下面错误" aria-hidden="true">#</a> 运行<code>npm run dev</code>之后，报下面错误<Badge type="tip" text="v1.3.2以下版本" vertical="top" /></h3>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211012092541299" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211012092541299.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>这是因为你在<code>docs/.vuepress/config.js</code>内有添加上下面代码</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>module<span class="token punctuation">.</span>exports <span class="token operator">=</span> <span class="token punctuation">{</span>
    <span class="token operator">...</span>
    <span class="token comment">//一定要添加下面代码</span>
     <span class="token function-variable function">onPrepared</span><span class="token operator">:</span> <span class="token keyword">async</span> <span class="token punctuation">(</span><span class="token parameter">app</span><span class="token punctuation">)</span> <span class="token operator">=></span> <span class="token punctuation">{</span>
        <span class="token keyword">const</span> myData <span class="token operator">=</span> app<span class="token punctuation">.</span>pages<span class="token punctuation">.</span><span class="token function">map</span><span class="token punctuation">(</span><span class="token punctuation">(</span><span class="token parameter">page</span><span class="token punctuation">)</span> <span class="token operator">=></span> <span class="token punctuation">{</span>
            <span class="token keyword">return</span> page<span class="token punctuation">;</span>
        <span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token keyword">await</span> app<span class="token punctuation">.</span><span class="token function">writeTemp</span><span class="token punctuation">(</span>
            <span class="token string">"my-data.js"</span><span class="token punctuation">,</span>
            <span class="token template-string"><span class="token template-punctuation string">`</span><span class="token string">export default </span><span class="token interpolation"><span class="token interpolation-punctuation punctuation">${</span><span class="token constant">JSON</span><span class="token punctuation">.</span><span class="token function">stringify</span><span class="token punctuation">(</span>myData<span class="token punctuation">)</span><span class="token interpolation-punctuation punctuation">}</span></span><span class="token template-punctuation string">`</span></span>
        <span class="token punctuation">)</span><span class="token punctuation">;</span>
    <span class="token punctuation">}</span><span class="token punctuation">,</span>
    <span class="token operator">...</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br></div></div><h3 id="首页文章列表没有显示文章更新时间" tabindex="-1"><a class="header-anchor" href="#首页文章列表没有显示文章更新时间" aria-hidden="true">#</a> 首页文章列表没有显示文章更新时间</h3>
<p>修改测试</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013102443617" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013102443617.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>如果在这里没有显示该文章的更新时间，这是因为你<code>docs/</code>下没有<code>.git</code>文件夹，因为此功能是通过git的<code>commit</code>获取的</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013102746980" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013102746980.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
